import { createContext, useEffect, useState } from "react";
import CompCurrent from "./comp/CompCurrent";
import CompDaily from "./comp/CompDaily";
import CompDetail from "./comp/CompDetail";
import CompHeader from "./comp/CompHeader";
import CompHourly from "./comp/CompHourly";
import CompMap from "./comp/CompMap";
import { fnGetAddress, fnGetcurrentLocation, fnGetWeather } from "./store/function";


export const AppContext = createContext()


const App = () => {
  let [stlatLng, stChangLatLng] = useState(null)
  let [stAddress, stChangAddress] = useState(null)
  let [stWeather, stChangWeatherData] = useState(null)// .
  //fnGetcurrentLocation()  
  //fnGetAddress(37.4905399,126.7280586) 
  //fnGetWeather(37.4905399, 126.7280586)

  const fnInit = async () => {
    let { lat, lng } = await fnGetcurrentLocation() //    (,) 
    let address = await fnGetAddress(lat, lng) //   
    let weather = await fnGetWeather(lat, lng) //    
    let bg = weather.current.weather[0].main
    document.getElementById('root').style.backgroundImage = `url(${process.env.PUBLIC_URL}/img/weather/${bg}.jpg)`
    stChangLatLng({ lat, lng })
    stChangAddress(address)
    stChangWeatherData(weather)
  }//fnInit

  useEffect(() => {
    fnInit()
  }, [])//useEffect

  return (
    <AppContext.Provider value={{
      stlatLng, stChangLatLng,
      stAddress, stChangAddress,
      stWeather, stChangWeatherData,
      fnInit
    }}>
      <CompHeader />
      <main className="app-container scr-style">
        <CompCurrent />
        <CompDetail />
        <CompHourly />
        <CompDaily />
      </main>
      <CompMap />
    </AppContext.Provider>
  );
};
export default App;
